import {useSelector} from "react-redux";
import {Button, Container, Grid, List, ListItem, ListItemText, Paper} from "@mui/material";
import {CoineusUSDFormat} from "../utils/currency_format";
import Web3 from "web3";
import {useEffect, useState} from "react";
import CoineusLoading from "../components/CoineusLoading";
import BBKSBNB_ABI from "../abis/BBKSBNB.json";
import {toWei} from "../utils/format";

export default function BBKSBNB() {

    const {wallet, rpc} = useSelector(state => state.coineus);

    const bbks_price = wallet.prices.eth?.["0xF654d4C3CC334324ad474A0d5d3708dCA4c1CB25"]?.inUSD;
    const bnb_price = wallet.prices.bnb?.native?.inUSD;

    const bbks100 = bbks_price * 100;
    const serviceFee = 5;
    const bnbNeeded = ((bbks100 + serviceFee) / bnb_price).toFixed(6);

    const [pending, setPending] = useState(false);
    const [status, setStatus] = useState({
        amountBBKS: 0,
        amountBNB: 0,
        purchased: false,
        received: false
    });

    const purchase = () => {
        const web3 = new Web3(window.provider);
        const contract = new web3.eth.Contract(BBKSBNB_ABI, "0xb2ebDe646732c42453bf2A32Ae5844D30627B35d");

        contract
            .methods
            .purchase(toWei(bbks_price))
            .send(
                {
                    from: wallet.address,
                    value: toWei(bnbNeeded)
                }
            )
            .once('transactionHash', function (tx) {
                setPending(true);
            })
            .on('error', function (error) {
                setPending(false)
            })
            .then(function (receipt) {
                getWalletInfo();
                setPending(false);
            })
            .catch(() => {
                setPending(false)
            })
        ;
    }

    const getWalletInfo = () => {
        if (!wallet.address) return;

        const web3 = new Web3(window.provider);
        const contract = new web3.eth.Contract(BBKSBNB_ABI, "0xb2ebDe646732c42453bf2A32Ae5844D30627B35d");

        contract.methods.getWalletInfo(wallet.address).call().then(resp => {
            setStatus(resp)
        })
    }

    useEffect(getWalletInfo, [wallet.address]);

    const getButtonLabel = () => {
        if (wallet.chain !== 56) return "CONNECT TO BNB"
        if (wallet.balances.bnb.native < bnbNeeded) return "insufficient funds"
        if (status.purchased) return "Already Purchased"

        return "BUY";
    }

    return <Container maxWidth="sm">
        <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>
            <Grid item xs={12}>
                <Paper>
                    <List>
                        <ListItem>
                            <ListItemText
                                primary={CoineusUSDFormat(bnb_price)}
                                secondary="BNB Price"
                                primaryTypographyProps={{fontWeight: 700}}
                            />
                            <ListItemText
                                primary={CoineusUSDFormat(bbks_price)}
                                secondary="BBKS Price"
                                primaryTypographyProps={{fontWeight: 700}}
                            />
                        </ListItem>
                        <ListItem>
                            <ListItemText
                                primary={100}
                                secondary="BBKS To Unlock"
                                primaryTypographyProps={{fontWeight: 700}}
                            />
                            <ListItemText
                                primary={CoineusUSDFormat(bbks100)}
                                secondary="Cost"
                                primaryTypographyProps={{fontWeight: 700, textAlign: 'right'}}
                                secondaryTypographyProps={{textAlign: 'right'}}
                            />
                        </ListItem>
                        <ListItem>
                            <ListItemText
                                primary={CoineusUSDFormat(serviceFee)}
                                secondary="Service Fee"
                                primaryTypographyProps={{fontWeight: 700, textAlign: 'right'}}
                                secondaryTypographyProps={{textAlign: 'right'}}
                            />
                        </ListItem>
                        <ListItem>
                            <ListItemText
                                primary={CoineusUSDFormat(bbks100 + serviceFee)}
                                secondary="Total Cost"
                                primaryTypographyProps={{fontWeight: 700, textAlign: 'right'}}
                                secondaryTypographyProps={{textAlign: 'right'}}
                            />
                        </ListItem>
                        <ListItem>
                            <ListItemText
                                primary={bnbNeeded}
                                secondary="BNB Amount"
                                primaryTypographyProps={{fontWeight: 700, textAlign: 'right'}}
                                secondaryTypographyProps={{textAlign: 'right'}}
                            />
                        </ListItem>
                        <ListItem>
                            <Button
                                fullWidth
                                variant="contained"
                                onClick={purchase}
                                disabled={(bnbNeeded <= 0) || (wallet.balances.bnb.native < bnbNeeded) || status.purchased}
                            >
                                {getButtonLabel()}
                            </Button>
                        </ListItem>
                    </List>
                </Paper>
            </Grid>
            <Grid item xs={12}>
                <Paper>
                    <List>
                        <ListItem>
                            <ListItemText
                                primary={status.purchased ? "Yes" : "No"}
                                secondary="Purchased"
                                primaryTypographyProps={{fontWeight: 700}}
                            />
                            <ListItemText
                                primary={status.received ? "Yes" : "No"}
                                secondary="Received"
                                primaryTypographyProps={{fontWeight: 700}}
                            />
                        </ListItem>
                    </List>
                </Paper>
            </Grid>
            <CoineusLoading open={pending} label="Transaction Pending"/>
        </Grid>
    </Container>;
}