import {useEffect, useState} from "react";
import axios from "axios";
import {
    Avatar, Button, Dialog,
    Grid, IconButton,
    Link,
    List,
    ListItem,
    ListItemAvatar,
    ListItemText,
    Paper,
    Typography
} from "@mui/material";

import {CoineusCryptoFormat} from "../utils/currency_format";
import {useSelector} from "react-redux";
import ReceiptLongIcon from '@mui/icons-material/ReceiptLong';

import ArrowUpwardIcon from '@mui/icons-material/ArrowUpward';
import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward';
import ArrowForwardIcon from '@mui/icons-material/ArrowForward';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import CoineusLoading from "./CoineusLoading";
import useTokenList from "../hooks/useTokenList";
import Web3 from "web3";

export default function Transactions(props) {

    const {network} = props;

    const {wallet} = useSelector(state => state.coineus);
    // const wallet = {address:"0x34fcfb7114d4bbd1164e24ed1155a9b8956b67c3"}

    const tokens = useTokenList();

    const [transactions, setTransactions] = useState([]);

    const [open, setOpen] = useState(false);
    const [pending, setPending] = useState(false);

    const endpoint = network === "eth" ? "https://api.thegraph.com/subgraphs/name/somemoecoding/surgeswap-v1-eth" : "https://api.thegraph.com/subgraphs/name/somemoecoding/surgeswap-v1";
    const scanner_url = network === "eth" ? "https://etherscan.io/" : "https://bscscan.com/";
    const transfer_filter = network === "eth" ? "0xcD682EF09d07668d49A8103ddD65Ff54AebFbfDe" : "0x9f19c8e321bD14345b797d43E01f0eED030F5Bff";

    useEffect(() => {
        if (!open) return;
        setPending(true);
        axios.post(
            endpoint,
            {
                query: `
                {
                  boughts(
                    first: 100
                    where:{
                      from :"${wallet.address}"
                    }
                    orderBy: blockTimestamp,
                    orderDirection:desc
                  ) {
                    from
                    to
                    transactionHash
                    blockTimestamp
                    tokens
                    beans
                    dollarBuy
                  }
                  solds(
                    first: 100
                    where:{
                      to :"${wallet.address}"
                    }
                    orderBy: blockTimestamp,
                    orderDirection:desc
                  ) {
                    from
                    to
                    transactionHash
                    blockTimestamp
                    tokens
                    beans
                    dollarSell
                  }
                  transfers_from: transfers(
                    first: 100
                    where:{
                      from :"${wallet.address}",
                      to_not: "${transfer_filter}"
                    }
                    orderBy: blockTimestamp,
                    orderDirection:desc
                  ) {
                    from
                    to
                    transactionHash
                    blockTimestamp
                    tokens: value
                  }
                  transfers_to: transfers(
                    first: 100
                    where:{
                      to :"${wallet.address}",
                      from_not: "${transfer_filter}"
                    }
                    orderBy: blockTimestamp,
                    orderDirection:desc
                  ) {
                    from
                    to
                    transactionHash
                    blockTimestamp
                    tokens: value
                  }
                }
            `
            }
        ).then(resp => {
            const {boughts, solds, transfers_to, transfers_from} = resp.data.data;

            boughts.map(b => {
                b.type = 'buy'
            })

            solds.map(b => {
                b.type = 'sell'
            })

            transfers_to.map(b => {
                b.type = 'transfers_to'
            })

            transfers_from.map(b => {
                b.type = 'transfers_from'
            })

            const tmp = [...boughts, ...solds, ...transfers_to, ...transfers_from];

            tmp.sort((a, b) => {
                if (a.blockTimestamp > b.blockTimestamp) {
                    return -1;
                }

                if (a.blockTimestamp < b.blockTimestamp) {
                    return 1;
                }

                return 0;
            })

            setTransactions(tmp);
            setPending(false);
        })
    }, [open])

    return <>
        <IconButton
            color="primary"
            size="small"
            onClick={() => setOpen(true)}
            style={{marginRight: -10, display: wallet.address !== undefined ? '' : 'none'}}
        ><ReceiptLongIcon/></IconButton>
        <Dialog
            open={open}
            fullScreen
            PaperProps={{style: {backgroundColor: '#000', backgroundImage: 'none', padding: 20}}}
        >
            <Grid container spacing={2}>
                <Grid item xs={12} textAlign="right">
                    <Button onClick={() => setOpen(false)}>Close</Button>
                </Grid>
                <Grid item xs={12}>

                    <List>
                        {
                            transactions.length === 0 &&
                            <Typography style={{padding: 20}}>No transactions</Typography>
                        }
                        {
                            transactions.map(t => {
                                return <Paper style={{marginBottom: 10}} key={t.transactionHash + t.tokens}>
                                    <ListItem key={t.transactionHash}>
                                        {checkTokenTransaction(wallet.address, t, tokens, network)}
                                    </ListItem>
                                </Paper>
                            })
                        }

                    </List>

                </Grid>
            </Grid>
        </Dialog>

        <CoineusLoading open={pending} label="Loading"/>
    </>
}

function checkTokenTransaction(wallet, transaction, tokens, network) {

    let found = false;
    let avatar = <Avatar/>;
    let secondaryText = '';
    const scanner_url = network === "eth" ? "https://etherscan.io/" : "https://bscscan.com/";

    const boxShadow = '0 0 5px 0';

    if (transaction.type === 'buy' || transaction.type === 'sell') {

        if (transaction.from.toLowerCase() === '0x9f19c8e321bd14345b797d43e01f0eed030f5bff') {
            //SURGE SELL
            found = true;
            avatar = <Avatar style={{boxShadow: `${boxShadow} #f00`}}
                             src={`https://coineus.app/assets/tokens/${network}/${Web3.utils.toChecksumAddress(transaction.from)}/logo.png`}/>
            secondaryText = `${CoineusCryptoFormat(transaction.beans / 10 ** 18)} ${network.toUpperCase()}`
        }

        if (transaction.to.toLowerCase() === '0x9f19c8e321bd14345b797d43e01f0eed030f5bff') {
            //SURGE BUY
            found = true;
            avatar = <Avatar style={{boxShadow:  `${boxShadow} #0f0`}}
                             src={`https://coineus.app/assets/tokens/${network}/${Web3.utils.toChecksumAddress(transaction.to)}/logo.png`}/>
            secondaryText = `${CoineusCryptoFormat(transaction.beans / 10 ** 18)} ${network.toUpperCase()}`
        }
    } else {
        tokens[network].map(token => {
            if (token.contract_address.toLowerCase() === transaction.from.toLowerCase()) {
                //TOKEN SELL
                found = true;
                avatar = <Avatar style={{boxShadow:  `${boxShadow} #f00`}}
                                 src={`https://coineus.app/assets/tokens/${network}/${Web3.utils.toChecksumAddress(transaction.from)}/logo.png`}/>
                secondaryText = "Token Sell"
            } else if (token.contract_address.toLowerCase() === transaction.to.toLowerCase()) {
                //TOKEN BUY
                found = true;
                avatar = <Avatar style={{boxShadow:  `${boxShadow} #0f0`}}
                                 src={`https://coineus.app/assets/tokens/${network}/${Web3.utils.toChecksumAddress(transaction.to)}/logo.png`}/>
                secondaryText = "Token Buy"
            }
        })
    }

    if (!found) {
        if (wallet.toLowerCase() === transaction.from.toLowerCase()) {
            //TRANSFER OUT
            avatar = <Avatar style={{boxShadow:  `${boxShadow} #fff`, backgroundColor: "#000"}}><ArrowBackIcon
                style={{color: '#fff'}}/></Avatar>;
            secondaryText = "Transfer Out"
        }

        if (wallet.toLowerCase() === transaction.to.toLowerCase()) {
            //TRANSFER IN
            avatar = <Avatar style={{boxShadow:  `${boxShadow} #723DF4`, backgroundColor: "#000"}}><ArrowForwardIcon
                style={{color: '#723DF4'}}/></Avatar>;
            secondaryText = "Transfer In"
        }
    }

    return <>
        <ListItemAvatar>
            {avatar}
        </ListItemAvatar>

        <ListItemText
            primary={(new Date(transaction.blockTimestamp * 1000)).toLocaleString()}
            primaryTypographyProps={{fontWeight: 700, fontSize: 12}}
            secondary={<Link href={`${scanner_url}/tx/${transaction.transactionHash}`}
                             target="_blank" style={{textDecoration: 'none'}}>View
                Tx</Link>}
            secondaryTypographyProps={{fontSize: 11}}
        />

        <ListItemText
            primary={<>
                {`${CoineusCryptoFormat(transaction.tokens / 10 ** 9)} SRG`}
            </>}
            secondary={secondaryText}
            primaryTypographyProps={{textAlign: 'right', fontWeight: 700, fontSize: 12}}
            secondaryTypographyProps={{textAlign: 'right', fontSize: 11}}
        />

    </>;

}